import React from "react"
import { Row, Col, Form, Icon, Divider, Typography } from "antd"
import { Heading, Button, Section, Loading, Card } from "components"
import moment from "moment"
import { Trans, t } from "@lingui/macro"
import { withI18n } from "@lingui/react"
import styled from "styled-components"
import { useState } from "react"
import { baseStyles } from "styles/base"
import Alert from "components/Alert"
import { media } from "helpers"
import DynamicIcon from "components/common/DynamicIcon"

const StyledCard = styled(Card)`
	&& {
		height: 200px;
		border: ${({ isSelected }) => isSelected && `2px solid ${baseStyles.primaryColor}`};
		background-color: ${({ isSelected }) => isSelected && baseStyles.primaryColorRgba};
	}

	${media.mobile`
        && {
            height: auto;
        }
    `}
`

const { Text, Paragraph } = Typography

function PreferField({ handlers, onData, loading, i18n }) {
	const { handleNext, handleUpdateProfile } = handlers
	const { userData = {}, setUserData, initialValues } = onData
	const [selectedField, setSelectedField] = useState({})

	const notSelectedYet = Object.keys(selectedField).length === 0

	const fieldOptions = [
		{
			key: "academic",
			title: <Trans>Academic</Trans>,
			description: <Trans>If you teach something like Mathematic, History, English, Chemical, etc</Trans>,
			call: "tutor"
		},
		{
			key: "non_academic",
			title: <Trans>Non-academic</Trans>,
			description: <Trans>If you teach something like Music, Photography, Digital Marketing, etc</Trans>,
			call: "coach"
		}
	]

	const handleSubmitPreferrableField = () => {
		localStorage.setItem("onboarding_preferrable_field", selectedField.key)
		handleNext("aboutme")
	}

	if (Object.keys(userData).length === 0) return <Loading />

	return (
		<Col lg={16} xs={24} style={{ padding: "2em 3em" }}>
			<Heading
				content={<Trans>Your preferrable field</Trans>}
				subheader={
					<Trans>
						<span>Tell us whether you'd like to teach academic or non-academic lessons</span>
					</Trans>
				}
				level={4}
				marginBottom="2em"
			/>
			<Alert
				type="info"
				showIcon
				message={
					<Trans>
						You can change this later, or even you can teach both of these fields. However, for now, you need to choose first
					</Trans>
				}
			/>
			<Row gutter={16}>
				{fieldOptions.map((item) => {
					const isSelected = item.key === selectedField.key
					return (
						<Col lg={12} key={item.key}>
							<StyledCard padding="2em" hoverable onClick={() => setSelectedField(item)} isSelected={isSelected}>
								<Typography className="mb2em">
									<Paragraph strong>{item.title}</Paragraph>
									<Text type="secondary">{item.description}</Text>
								</Typography>
								{isSelected && (
									<Text>
										<DynamicIcon type="icon-male" /> Then we'll call you <strong>{item.call}</strong>
									</Text>
								)}
							</StyledCard>
						</Col>
					)
				})}
			</Row>

			<Divider className="mb4em" />

			<Section textAlign="right" noPadding>
				<Button type="primary" onClick={handleSubmitPreferrableField} loading={loading} disabled={notSelectedYet}>
					{loading ? <Trans>Please wait...</Trans> : <Trans>Next</Trans>} <Icon type="right" />
				</Button>
			</Section>
		</Col>
	)
}

export default withI18n()(PreferField)
