import React from "react"
import { Row, Col, Form, Icon, Divider } from "antd"
import { Heading, Button, Section, Loading } from "components"
import { Formik } from "formik"
import TextInput from "components/forms/TextInput"
import moment from "moment"
import { Trans, t } from "@lingui/macro"
import { withI18n } from "@lingui/react"

function Name({ handlers, onData, loading, i18n }) {
	const { handleNext, handleUpdateProfile } = handlers
	const { userData = {}, setUserData, initialValues } = onData

	const handleSubmitName = (values, { setSubmitting }) => {
		values = {
			...initialValues,
			...values,
			dob: moment(initialValues.dob).format("YYYY/MM/DD"),
		}
		// updateProfile({ variables: { ...values } })
		handleUpdateProfile(values, "preferfield")
		setSubmitting(false)
	}

	if (Object.keys(userData).length === 0) return <Loading />

	return (
		<Col lg={16} xs={24} style={{ padding: "2em 3em" }}>
			<Heading
				content={<Trans>Your name</Trans>}
				subheader={
					<Trans>
						<span>Tell us your name</span>
					</Trans>
				}
				level={4}
				marginBottom="2em"
			/>
			<Formik
				onSubmit={handleSubmitName}
				validate={validation}
				initialValues={{
					firstName: userData.firstName || "",
					lastName: userData.lastName || "",
				}}
			>
				{({ handleSubmit }) => (
					<Form layout="vertical" onSubmit={handleSubmit}>
						<Row gutter={32}>
							<Col lg={12}>
								<TextInput name="firstName" label={<Trans>First name</Trans>} placeholder={i18n._(t`Your first name...`)} />
							</Col>
							<Col lg={12}>
								<TextInput name="lastName" label={<Trans>Last name</Trans>} placeholder={i18n._(t`Your last name...`)} />
							</Col>
						</Row>

						<Divider className="mb4em" />

						<Section textAlign="right" noPadding>
							<Button type="primary" htmlType="submit" loading={loading}>
								{loading ? <Trans>Please wait...</Trans> : <Trans>Next</Trans>} <Icon type="right" />
							</Button>
						</Section>
					</Form>
				)}
			</Formik>
		</Col>
	)
}

const validation = (values) => {
	let errors = {}
	if (!values.firstName) errors.firstName = "You have to tell us your first name"
	if (!values.lastName) errors.lastName = "You have to tell us your last name"
	return errors
}

export default withI18n()(Name)
