import React, { useState, useEffect } from "react"
import { Row, Col, Form, Icon, Popconfirm, message, Divider } from "antd"
import { Heading, Button, ButtonLink, Empty } from "components"
import styled from "styled-components"

import TextInput from "components/forms/TextInput"
import SelectInput from "components/forms/SelectInput"
import { useMutation } from "@apollo/react-hooks"
import { mutateExperiences, mutateDeleteExperiences, mutateUpdateExperiences } from "queries/profile"
import { client, mobile, newClient, renderError } from "helpers"
import { Formik } from "formik"
import DateInput from "components/forms/DateInput"
import CheckboxInput from "components/forms/CheckboxInput"
import { SubmitButton } from "formik-antd"
import moment from "moment"
import { Trans, t } from "@lingui/macro"
import { withI18n } from "@lingui/react"
import { useLifelongCategories } from "helpers/hooks/data"

const FieldRow = styled(Row).attrs({
	gutter: 16
})`
	position: relative;
	&:hover {
		.delete {
			display: flex;
			justify-content: center;
			align-items: center;
			visibility: visible;
		}
	}
	.delete {
		display: none;
		cursor: pointer;
		visibility: hidden;
		position: absolute;
		right: 0;
		top: -20px;
		border-radius: 50%;
		width: 25px;
		color: #ff9d00;
		height: 25px;
		z-index: 99;
		&:hover {
			background: #eee;
		}
	}
`

function Experiences({ onNext, onPrev, onExperiences, data, fetchUser, loading, i18n }) {
	const [userExperiences, setUserExperiences] = useState([])
	const [isAdding, setIsAdding] = useState(false)
	const { options: nonAcademicOptions } = useLifelongCategories()

	const preferrableField = localStorage.getItem("onboarding_preferrable_field")
	const isNonAcademic = preferrableField === "non_academic"
	const { dataSubjects = {}, dataLevels = {}, userData = {} } = data

	const [addExperience] = useMutation(mutateExperiences, {
		client: client.profileClient,
		onCompleted: () => {
			fetchUser()
			message.success(i18n._(t`The experience item has been added`), 2)
		},
		onError: (err) => renderError(err)
	})

	const [updateExperience] = useMutation(mutateUpdateExperiences, {
		client: client.profileClient,
		onCompleted: () => {
			fetchUser()
			message.success(i18n._(t`The experience item has been updated`), 2)
		},
		onError: (err) => renderError(err)
	})

	const [deleteExperiences] = useMutation(mutateDeleteExperiences, {
		client: newClient(),
		onCompleted: () => {
			fetchUser()
			setIsAdding(false)
			message.success(i18n._(t`The experience item has been deleted`), 2)
		},
		onError: (err) => renderError(err)
	})

	const levelOptions = (dataLevels.getProgrammeLevelList || []).map((item) => ({
		value: item.name,
		label: item.name
	}))

	let subjectOptions = (dataSubjects.getProgrammeSubjectList || []).map((item) => ({
		value: item.name,
		label: item.name
	}))

	subjectOptions = isNonAcademic ? nonAcademicOptions : subjectOptions

	const handleCancel = () => {
		setUserExperiences(userData.experienceList)
		setIsAdding(false)
	}

	const handleSave = (values, { setSubmitting }) => {
		const hasPvid = values.hasOwnProperty("pvid")
		const handler = hasPvid ? updateExperience : addExperience

		values = {
			...values,
			level: "0",
			fromDate: moment(values.fromDate).format("YYYY-MM-DD"),
			toDate: moment(values.toDate).format("YYYY-MM-DD")
		}

		if (hasPvid) {
			const { source, ...rest } = values
			values = rest
		}
		handler({ variables: { list: [values] } }).finally(() => {
			setIsAdding(false)
			setSubmitting(false)
		})
	}

	const handleDelete = (pvid) => {
		const filtered = userExperiences.find((item) => item.pvid === pvid) || {}
		deleteExperiences({ variables: { pvidList: [filtered.pvid] } })
	}

	const handleAddNewField = () => setIsAdding(true)

	const renderExperiences = () => {
		if (isAdding)
			return (
				<Formik onSubmit={handleSave} initialValues={{ isPresent: false, isPrivateTutor: false }}>
					{({ handleSubmit, values }) => (
						<Form layout="vertical" onSubmit={handleSubmit} style={{ marginBottom: "2em" }}>
							<Row gutter={16} type="flex">
								<Col lg={isNonAcademic ? 12 : 24} xs={isNonAcademic ? 12 : 24}>
									<TextInput
										name="institution"
										placeholder={i18n._(t`Tuition's name`)}
										label={<Trans>Tuition's name</Trans>}
										disabled={values.isPrivateTutor}
									/>
								</Col>
								{!isNonAcademic && (
									<Col lg={12} xs={12}>
										<SelectInput
											name="curriculum"
											label={<Trans>Curriculum</Trans>}
											placeholder={i18n._(t`Curriculum`)}
											options={levelOptions}
										/>
									</Col>
								)}
								<Col lg={12} xs={12}>
									<SelectInput
										cascader={isNonAcademic}
										name="subject"
										label={<Trans>Subject</Trans>}
										placeholder={i18n._(t`Subject`)}
										options={subjectOptions}
									/>
								</Col>
							</Row>
							<Row gutter={16}>
								<Col lg={12} xs={12}>
									<DateInput
										monthly
										name="fromDate"
										label={<Trans>Date joined</Trans>}
										placeholder={i18n._(t`Date joined`)}
										style={{ width: "100%" }}
										disabledDate={(current) => current && current > moment()}
									/>
								</Col>
								<Col lg={12} xs={12}>
									<DateInput
										monthly
										name="toDate"
										label={<Trans>Date resigned</Trans>}
										placeholder={i18n._(t`Date resigned`)}
										style={{ width: "100%" }}
										disabledDate={(current) => current && current > moment()}
										disabled={values.isPresent}
									/>
								</Col>
								<Col lg={24} xs={24}>
									<CheckboxInput name="isPrivateTutor">
										<Trans>I was a private tutor</Trans>
									</CheckboxInput>
								</Col>
							</Row>
							<Row>
								<Col lg={24} xs={24} style={{ marginBottom: "2em" }}>
									<CheckboxInput name="isPresent">
										<Trans>I'm currently working here</Trans>
									</CheckboxInput>
								</Col>
							</Row>
							<SubmitButton block>
								<Trans>Save this experience</Trans>
							</SubmitButton>{" "}
							&nbsp;{" "}
							<ButtonLink block onClick={handleCancel}>
								<Trans>Cancel</Trans>
							</ButtonLink>
						</Form>
					)}
				</Formik>
			)

		if ((userExperiences || []).length === 0) return <Empty description={<Trans>No experiences data</Trans>} />

		return (userExperiences || []).map((item) => (
			<Formik enableReinitialize key={item.pvid} onSubmit={handleSave} initialValues={item}>
				{({ handleSubmit, values, dirty }) => (
					<Form layout="vertical" onSubmit={handleSubmit} style={{ marginBottom: "2em" }}>
						<FieldRow>
							<Col lg={24}>
								<Row gutter={16}>
									<Col lg={24} xs={24}>
										<TextInput
											name="institution"
											label={<Trans>Tuition's name</Trans>}
											placeholder={i18n._(t`Tuition's name`)}
											disabled={values.isPrivateTutor || item.isPrivateTutor}
										/>
									</Col>
									<Col lg={12} xs={12}>
										<SelectInput
											name="curriculum"
											label={<Trans>Curriculum</Trans>}
											placeholder={i18n._(t`Curriculum`)}
											options={levelOptions}
										/>
									</Col>
									<Col lg={12} xs={12}>
										<SelectInput
											name="subject"
											label={<Trans>Subject</Trans>}
											placeholder={i18n._(t`Subject`)}
											options={subjectOptions}
										/>
									</Col>
								</Row>
								<Row gutter={16} style={{ marginBottom: "2em" }}>
									<Col lg={12} xs={12}>
										<DateInput
											monthly
											name="fromDate"
											label={<Trans>Date joined</Trans>}
											placeholder={i18n._(t`Date joined`)}
											style={{ width: "100%" }}
											disabledDate={(current) => current && current > moment()}
										/>
									</Col>
									<Col lg={12} xs={12}>
										<DateInput
											monthly
											name="toDate"
											label={<Trans>Date resigned</Trans>}
											placeholder={i18n._(t`Date resigned`)}
											disabled={values.isPresent || item.isPresent}
											style={{ width: "100%" }}
											disabledDate={(current) => current && current > moment()}
										/>
									</Col>
									<Col lg={24}>
										<CheckboxInput name="isPrivateTutor">
											<Trans>I was a private tutor</Trans>
										</CheckboxInput>
									</Col>
									<Col lg={24}>
										<CheckboxInput name="isPresent">
											<Trans>I'm currently working here</Trans>
										</CheckboxInput>
									</Col>
								</Row>
								<SubmitButton type="primary" disabled={!dirty}>
									<Trans>Update</Trans>
								</SubmitButton>{" "}
								&nbsp;{" "}
								<Popconfirm title={<Trans>Delete this experience?</Trans>} onConfirm={() => handleDelete(item.pvid)}>
									<ButtonLink icon="close">
										&nbsp; <Trans>Delete</Trans>
									</ButtonLink>
								</Popconfirm>
							</Col>
						</FieldRow>
					</Form>
				)}
			</Formik>
		))
	}

	useEffect(() => {
		setUserExperiences(userData.experienceList)
	}, [userData.experienceList])

	console.log({ data })

	return (
		<Col lg={16} xs={24} style={{ padding: "2em" }}>
			<Heading
				content={<Trans>Tutoring experiences</Trans>}
				subheader={<Trans>Please fill in your tutoring experiences</Trans>}
				level={4}
				marginBottom="2em"
			/>

			{renderExperiences()}

			{!isAdding && (
				<div style={{ marginBottom: "2em" }}>
					<Button block type="dashed" size="medium" icon="plus" onClick={handleAddNewField} style={{ marginBottom: "2em" }}>
						&nbsp; <Trans>Add a new experience</Trans>
					</Button>
					{/* <ButtonLink block icon="close" onClick={() => ({})}>
						&nbsp; <Trans>Cancel</Trans>
					</ButtonLink> */}
				</div>
			)}

			<Divider className="mb4em" />

			<Row type="flex" justify="space-between">
				<Col lg={12} xs={24}>
					<ButtonLink type="primary" block={mobile} size="large" onClick={() => onPrev("educations")}>
						<Trans>
							<Icon type="left" /> Back to Educations
						</Trans>
					</ButtonLink>
				</Col>
				<Col lg={12} xs={24} style={{ textAlign: !mobile && "right" }}>
					<Button type="primary" block={mobile} size="large" htmlType="submit" onClick={() => onNext("certifications")}>
						<Trans>
							Go to Certifications <Icon type="right" />
						</Trans>
					</Button>
				</Col>
			</Row>
		</Col>
	)
}

export default withI18n()(Experiences)
