const stepsArray = (pathname) => {
	switch (pathname) {
		case "name":
			return 0
		case "preferfield":
			return 1
		case "aboutme":
			return 2
		case "subjects":
			return 3
		case "educations":
			return 4
		case "experiences":
			return 5
		case "certifications":
			return 6
		case "done":
			return 7
		default:
			return -1
	}
}

const programmeClient = newClient("programme")

function AdditionalInfoTutor({ history, location, country, i18n }) {
	const pathname = (location.pathname || "").split("/")[4]
	const userDetails = JSON.parse(localStorage.getItem("userDetails")) || {}

	const { data: dataSubjects = {} } = useQuery(queryFetchSubjects, {
		client: programmeClient,
		variables: { country: userDetails.country }
	})
	const { data: dataLevels = {} } = useQuery(queryFetchLevels, {
		client: programmeClient,
		variables: { country: userDetails.country }
	})
	const { data: dataCurriculums = {} } = useQuery(queryFetchCurriculums, {
		client: programmeClient,
		variables: { country: userDetails.country }
	})
	const [activeStep, setActiveStep] = useState(stepsArray(pathname))
	const [aboutme, setAboutme] = useState("")
	const [educations, setEducations] = useState(initialValues.educations)
	const [experiences, setExperiences] = useState(initialValues.experiences)
	const [certifications, setCertifications] = useState([])
	const [userData, setUserData] = useState({})

	const [fetchUser, { loading: loadingFetch }] = useMutation(mutateUserProfile, {
		client: newClient(),
		onCompleted: (data) => {
			data = data.getProfile || {}
			setUserData(data)
			setAboutme(data.description)
			setCertifications(data.certificateList)
		}
	})

	const [updateProfile] = useMutation(mutateUpdateProfile, {
		client: newClient("account"),
		onCompleted: (data) => {
			setUserData(data)
			fetchUser()
			// handleNext("basic")
		}
	})

	const handleUpdateProfile = (values, section) => {
		updateProfile({ variables: { ...values } }).then(() => handleNext(section))
	}

	const handleNext = (section) => {
		setActiveStep((activeStep) => activeStep + 1)
		history.push(`/${country}/profile/onboarding_steps/${section.toLowerCase()}`)
	}

	const handlePrev = (section) => {
		setActiveStep((activeStep) => activeStep - 1)
		history.push(`/${country}/profile/onboarding_steps/${section.toLowerCase()}`)
	}

	const handleSkipSteps = () => {
		Modal.confirm({
			title: i18n._(t`Skip this?`),
			content: i18n._(t`Are you sure want to skip this at the moment?`),
			centered: true,
			onOk() {
				history.push(`/${country}`)
			}
		})
	}

	const steps = stepsArray(pathname)

	useEffect(() => {
		const isFirstTime = localStorage.getItem("firstTime") === "true"
		// if (!localStorage.getItem("token") || !isFirstTime) history.push(`/${country}/404`)
		fetchUser()
		setActiveStep(steps)
	}, [country, history, steps])

	return (
		<Row type="flex" justify="center" align="middle" style={{ marginTop: !mobile && "2em" }}>
			<Col lg={20} xs={24}>
				<Section textAlign="center" centered width={mobile ? "100%" : "80%"}>
					<StyledHeading
						content={<Trans>You are almost done</Trans>}
						subheader={
							<Trans>
								A better profile is the one that has a lot of informations to dig in. Therefore, it's recommended for you,
								as a tutor, to fill out additional informations you might have to complete your profile.
							</Trans>
						}
					/>
				</Section>
				<StyledCard>
					<Donut src={orangeEl} bottom="-140px" left="-180px" style={{ zIndex: -1 }} />
					<Donut src={purpleEl} width="200px" top="-90px" right="-90px" style={{ zIndex: -1 }} />
					<Row type="flex">
						{!mobile && (
							<Col lg={8} xs={24}>
								<BackgroundSection>
									<div className="steps">
										<Steps
											progressDot
											current={activeStep}
											steps={[
												"Name",
												"Prefer field",
												"About me",
												"Subjects",
												"Educations",
												"Experiences",
												"Certifications",
												"Done"
											]}
										/>
									</div>
									<div>
										<GifPlayer
											src="https://assets1.lottiefiles.com/packages/lf20_IJauXS.json"
											style={{ width: "100%", height: "80%", position: "relative", top: 70 }}
										/>
									</div>
								</BackgroundSection>
							</Col>
						)}
						{/* <Suspense fallback={<Loading />}> */}
						<Switch>
							<Redirect
								exact
								from={`/${country}/profile/onboarding_steps`}
								to={`/${country}/profile/onboarding_steps/name`}
							/>
							<Route
								path={`/:country/profile/onboarding_steps/name`}
								render={() => (
									<Name
										onActiveStep={{ activeStep, setActiveStep }}
										handlers={{ handleNext, handleUpdateProfile }}
										onAboutme={{ aboutme, setAboutme }}
										onData={{ userData, setUserData, initialValues: initialValues.aboutme }}
									/>
								)}
							/>
							<Route
								path={`/:country/profile/onboarding_steps/preferfield`}
								render={() => (
									<PreferField
										onActiveStep={{ activeStep, setActiveStep }}
										handlers={{ handleNext, handleUpdateProfile }}
										onAboutme={{ aboutme, setAboutme }}
										onData={{ userData, setUserData, initialValues: initialValues.aboutme }}
									/>
								)}
							/>
							<Route
								path={`/:country/profile/onboarding_steps/aboutme`}
								render={() => (
									<AboutMe
										onActiveStep={{ activeStep, setActiveStep }}
										handlers={{ handleNext, handlePrev, handleUpdateProfile }}
										onAboutme={{ aboutme, setAboutme }}
										onData={{ userData }}
									/>
								)}
							/>
							<Route
								path={`/:country/profile/onboarding_steps/subjects`}
								render={() => (
									<Subjects
										onActiveStep={{ activeStep, setActiveStep }}
										handlers={{ handleNext, handlePrev }}
										onData={{ userData }}
										loading={loadingFetch}
										fetchUser={fetchUser}
									/>
								)}
							/>
							<Route
								path={`/:country/profile/onboarding_steps/educations`}
								render={() => (
									<Educations
										onActiveStep={{ activeStep, setActiveStep }}
										onNext={handleNext}
										onPrev={handlePrev}
										onData={{ userData }}
										onEducations={{ educations, setEducations }}
										loading={loadingFetch}
										fetchUser={fetchUser}
									/>
								)}
							/>
							<Route
								path={`/:country/profile/onboarding_steps/experiences`}
								render={() => (
									<Experiences
										onActiveStep={{ activeStep, setActiveStep }}
										onNext={handleNext}
										onPrev={handlePrev}
										data={{ dataSubjects, dataCurriculums, dataLevels, userData }}
										onExperiences={{ experiences, setExperiences }}
										fetchUser={fetchUser}
										loading={loadingFetch}
									/>
								)}
							/>
							<Route
								path={`/:country/profile/onboarding_steps/certifications`}
								render={() => (
									<Certifications
										onActiveStep={{ activeStep, setActiveStep }}
										onNext={handleNext}
										onPrev={handlePrev}
										fetchUser={fetchUser}
										onCertifications={{ certifications, setCertifications }}
									/>
								)}
							/>
						</Switch>
						{/* </Suspense> */}
					</Row>
				</StyledCard>
				<Section centered textAlign="center">
					{activeStep > 0 && (
						<ButtonLink onClick={handleSkipSteps}>
							<Trans>
								Let me fill out everything later <Icon type="right" />{" "}
							</Trans>
						</ButtonLink>
					)}
				</Section>
			</Col>
		</Row>
	)
}

export default withRouter(withI18n()(AdditionalInfoTutor))
