import React, { useState, useEffect } from "react"
import { Row, Col, Form, Icon, message, Popconfirm, Divider } from "antd"
import { Heading, ButtonLink, Button, Empty, Loading } from "components"
import styled from "styled-components"
import uuid from "uuid/v4"

import TextInput from "components/forms/TextInput"
import SelectInput from "components/forms/SelectInput"
import { useMutation, useQuery } from "@apollo/react-hooks"
import { mutateAddSubjects, mutateDeleteSubjects, mutateUpdateSubjects } from "queries/profile"
import { mobile, newClient, renderError } from "helpers"
import { Formik } from "formik"
import { queryFetchSubjects, queryFetchLevels } from "queries/data"
import { SubmitButton, FormikDebug, Cascader } from "formik-antd"
import { Trans, t } from "@lingui/macro"
import { withI18n } from "@lingui/react"
import { countryMap } from "helpers/countryMapping"
import { useLifelongCategories } from "helpers/hooks/data"
import { MUTATE_SUBJECT_NON_ACADEMIC } from "queries/user"

const FieldRow = styled(Row).attrs({
	gutter: 16
})`
	position: relative;
	margin-bottom: 2em;
	&:hover {
		.delete {
			display: flex;
			justify-content: center;
			align-items: center;
			visibility: visible;
		}
	}
	.delete {
		display: none;
		cursor: pointer;
		visibility: hidden;
		position: absolute;
		right: 0;
		top: -20px;
		border-radius: 50%;
		width: 25px;
		color: #ff9d00;
		height: 25px;
		z-index: 99;
		&:hover {
			background: #eee;
		}
	}
`

const programmeClient = newClient("programme")

function Subjects({ handlers, onData, fetchUser, loading, i18n }) {
	const preferrableField = localStorage.getItem("onboarding_preferrable_field")
	const { userData = {} } = onData
	const subjectListNonAcademic = (userData.nonAcademic || []).map((item) => item.subNonAcademic).flat()
	const userSubjectList = preferrableField === "non_academic" ? subjectListNonAcademic : userData.subjectList

	const [isAdding, setIsAdding] = useState(false)
	const { handleNext, handlePrev } = handlers
	const [userSubjects, setUserSubjects] = useState(userSubjectList)
	const { options } = useLifelongCategories()

	const userDetails = JSON.parse(localStorage.getItem("userDetails")) || {}

	const { data: dataSubjects = {} } = useQuery(queryFetchSubjects, {
		client: programmeClient,
		variables: { country: userDetails.country || "sg" }
	})

	const { data: dataCurriculums = {} } = useQuery(queryFetchLevels, {
		client: programmeClient,
		variables: { country: userDetails.country || "sg" }
	})

	const subjectOptions = (dataSubjects.getProgrammeSubjectList || []).map((item) => ({
		value: Number(item.pvid),
		label: item.name
	}))

	const currOptions = (dataCurriculums.getProgrammeLevelList || []).map((item) => ({
		value: Number(item.pvid),
		label: item.name
	}))

	const [addSubjects, { loading: loadingAdd }] = useMutation(mutateAddSubjects, {
		client: newClient(),
		onCompleted: () => {
			fetchUser().then(() => message.success(i18n._(t`The subject item has been added`), 2))
		},
		onError: (err) => renderError(err)
	})

	const [updateSubjects, { loading: loadingUpdate }] = useMutation(mutateUpdateSubjects, {
		client: newClient(),
		onCompleted: () => {
			fetchUser().then(() => message.success(i18n._(t`The subject item has been updated`), 2))
		},
		onError: (err) => renderError(err)
	})

	const [deleteSubjects, { loading: loadingDelete }] = useMutation(mutateDeleteSubjects, {
		client: newClient(),
		onCompleted: () => {
			fetchUser().then(() => message.success(i18n._(t`The subject item has been deleted`), 2))
		},
		onError: (err) => renderError(err)
	})

	const [updateSubjectsNonAcademic] = useMutation(MUTATE_SUBJECT_NON_ACADEMIC, {
		client: newClient(),
		onCompleted: () => {
			fetchUser().then(() => message.success(i18n._(t`The subject item has been updated`), 2))
		},
		onError: (err) => renderError(err)
	})

	const handleAddSubject = (values, { setSubmitting }) => {
		const hasPvid = values.hasOwnProperty("pvid")
		const updateHandler = preferrableField === "non_academic" ? updateSubjectsNonAcademic : updateSubjects
		const addHandler = preferrableField === "non_academic" ? updateSubjectsNonAcademic : addSubjects

		if (hasPvid) {
			const { source, ...rest } = values
			values = rest
			values = {
				...values,
				orderList: 1,
				pricePerHour: Number(values.pricePerHour),
				programmeSubjectSubNonAcademicPvid: String(values.programmeSubjectSubNonAcademicPvid[1])
			}
		} else {
			values = {
				...values,
				orderList: 1,
				pricePerHour: Number(values.pricePerHour),
				programmeSubjectSubNonAcademicPvid: values.programmeSubjectSubNonAcademicPvid[1]
			}
		}

		const handler = hasPvid ? updateHandler : addHandler

		handler({ variables: { list: [values] } }).finally(() => {
			setSubmitting(false)
			setIsAdding(false)
		})
	}

	const handleDelete = (pvid) => {
		const filtered = userSubjects.find((item) => item.pvid === pvid) || {}
		if (preferrableField === "non_academic") {
			filtered.programmeSubjectSubNonAcademicPvid = filtered.pvid
			filtered.isDeleted = true
			const { name, __typename, status, programmeSubjectNonAcademicPvid, pvid, ...actual } = filtered
			updateSubjectsNonAcademic({ variables: { list: [actual] } })
		} else deleteSubjects({ variables: { pvidList: [filtered.pvid] } })
	}

	const handleCancel = () => {
		setIsAdding(false)
		setUserSubjects(userSubjectList)
	}

	const handleAddNewField = () => {
		setIsAdding(true)
		setUserSubjects((prev) => [
			...prev,
			{
				id: uuid(),
				programmeSubjectPvid: "",
				pricePerHour: 0,
				subject: { pvid: null },
				level: { pvid: null },
				orderList: 0
			}
		])
	}

	const renderSubjects = () => {
		if (isAdding) {
			if (preferrableField === "non_academic") {
				return (
					<Formik onSubmit={handleAddSubject} initialValues={{ programmeSubjectSubNonAcademicPvid: "", pricePerHour: 0 }}>
						{({ handleSubmit }) => (
							<Form layout="vertical" onSubmit={handleSubmit} className="mb2em">
								<Row gutter={16} className="mb0">
									<Col lg={12} xs={24}>
										<SelectInput
											cascader
											name="programmeSubjectSubNonAcademicPvid"
											placeholder={i18n._(t`Subject name`)}
											label={<Trans>Subject</Trans>}
											options={options}
											marginBottom="5px"
										/>
									</Col>
									<Col lg={12} xs={12}>
										<TextInput
											name="pricePerHour"
											label={<Trans>Price per hour</Trans>}
											placeholder={i18n._(t`Your price per hour...`)}
											addonBefore={countryMap[userDetails.country].symbol}
										/>
									</Col>
								</Row>
								<SubmitButton block>
									<Trans>Save this subject</Trans>
								</SubmitButton>{" "}
								&nbsp;{" "}
								<ButtonLink block onClick={handleCancel}>
									<Trans>Cancel</Trans>
								</ButtonLink>
							</Form>
						)}
					</Formik>
				)
			}

			return (
				<Formik onSubmit={handleAddSubject} initialValues={{ pricePerHour: 0 }}>
					{({ handleSubmit }) => (
						<Form layout="vertical" onSubmit={handleSubmit} className="mb2em">
							<Row gutter={16} type="flex" className="mb0">
								<Col lg={12} xs={24}>
									<SelectInput
										name="programmeSubjectPvid"
										placeholder={i18n._(t`Subject name`)}
										label={<Trans>Subject</Trans>}
										options={subjectOptions}
										marginBottom="5px"
									/>
								</Col>
								<Col lg={12} xs={24}>
									<SelectInput
										name="programmeLevelPvid"
										placeholder={i18n._(t`Curriculum`)}
										label={<Trans>Curriculum</Trans>}
										options={currOptions}
										marginBottom="5px"
									/>
								</Col>
								<Col lg={12}>
									<TextInput
										label={<Trans>Price per hour</Trans>}
										name="pricePerHour"
										addonBefore={countryMap[userDetails.country].symbol}
									/>
								</Col>
							</Row>
							<SubmitButton block>
								<Trans>Save this subject</Trans>
							</SubmitButton>{" "}
							&nbsp;{" "}
							<ButtonLink block onClick={handleCancel}>
								<Trans>Cancel</Trans>
							</ButtonLink>
						</Form>
					)}
				</Formik>
			)
		}
		if ((userSubjects || []).length === 0) return <Empty />
		if (loading || loadingDelete || loadingAdd || loadingUpdate) return <Loading />

		return (userSubjects || []).map((item) => {
			if (preferrableField === "non_academic")
				return (
					<Formik
						enableReinitialize
						onSubmit={handleAddSubject}
						initialValues={{
							programmeSubjectSubNonAcademicPvid: [item.programmeSubjectSubNonAcademicPvid, item.pvid],
							pricePerHour: item.pricePerHour,
							profileNonAcademicPvid: item.profileNonAcademicPvid
						}}
					>
						{({ handleSubmit }) => (
							<Form layout="vertical" onSubmit={handleSubmit} className="mb2em">
								<Row gutter={16} className="mb0">
									<Col lg={12} xs={24}>
										<SelectInput
											cascader
											name="programmeSubjectSubNonAcademicPvid"
											placeholder={i18n._(t`Subject name`)}
											label={<Trans>Subject</Trans>}
											options={options}
											marginBottom="5px"
										/>
									</Col>
									<Col lg={12} xs={12}>
										<TextInput
											name="pricePerHour"
											label={<Trans>Price per hour</Trans>}
											placeholder={i18n._(t`Your price per hour...`)}
											addonBefore={countryMap[userDetails.country].symbol}
										/>
									</Col>
								</Row>
								<SubmitButton type="primary">
									<Trans>Update</Trans>
								</SubmitButton>{" "}
								&nbsp;{" "}
								<Popconfirm title={<Trans>Delete this subject?</Trans>} onConfirm={() => handleDelete(item.pvid)}>
									<ButtonLink icon="close">
										&nbsp; <Trans>Delete</Trans>
									</ButtonLink>
								</Popconfirm>
							</Form>
						)}
					</Formik>
				)

			return (
				<Formik
					enableReinitialize
					key={item.pvid}
					onSubmit={handleAddSubject}
					initialValues={{
						pvid: item.pvid,
						programmeSubjectPvid: Number(item.subject.pvid),
						programmeLevelPvid: Number(item.level.pvid),
						pricePerHour: item.pricePerHour
					}}
				>
					{({ handleSubmit }) => (
						<FieldRow>
							<Col lg={24}>
								<Form layout="vertical" onSubmit={handleSubmit}>
									<Row gutter={16} type="flex">
										<Col lg={12} xs={24}>
											<SelectInput
												name="programmeSubjectPvid"
												placeholder={i18n._(t`Subject name`)}
												label={<Trans>Subject</Trans>}
												options={subjectOptions}
												marginBottom="5px"
											/>
										</Col>
										<Col lg={12} xs={24}>
											<SelectInput
												name="programmeLevelPvid"
												placeholder={i18n._(t`Curriculum`)}
												label={<Trans>Curriculum</Trans>}
												options={currOptions}
												marginBottom="5px"
											/>
										</Col>
										<Col lg={12} xs={12}>
											<TextInput
												name="pricePerHour"
												label={<Trans>Price per hour</Trans>}
												placeholder={i18n._(t`Your price per hour...`)}
												addonBefore={countryMap[userDetails.country].symbol}
											/>
										</Col>
									</Row>
									<SubmitButton type="primary">
										<Trans>Update</Trans>
									</SubmitButton>{" "}
									&nbsp;{" "}
									<Popconfirm title={<Trans>Delete this subject?</Trans>} onConfirm={() => handleDelete(item.pvid)}>
										<ButtonLink icon="close">
											&nbsp; <Trans>Delete</Trans>
										</ButtonLink>
									</Popconfirm>
								</Form>
							</Col>
						</FieldRow>
					)}
				</Formik>
			)
		})
	}

	useEffect(() => {
		setUserSubjects(userSubjectList)
	}, [userSubjectList])

	return (
		<Col lg={16} xs={24} style={{ padding: "2em" }}>
			<Heading
				content={<Trans>Subjects</Trans>}
				subheader={<Trans>Please fill the subjects you are teaching</Trans>}
				level={4}
				marginBottom="2em"
			/>

			{renderSubjects()}

			{!isAdding && (
				<div className="mb2em">
					<Button block type="dashed" size="medium" icon="plus" onClick={handleAddNewField} className="mb2em">
						&nbsp; <Trans>Add a new subject</Trans>
					</Button>
				</div>
			)}

			<Divider className="mb4em" />

			<Row type="flex" justify="space-between">
				<Col lg={12} xs={24}>
					<ButtonLink type="primary" size="large" block={mobile} onClick={() => handlePrev("aboutme")}>
						<Trans>
							<Icon type="left" /> Back to About me
						</Trans>
					</ButtonLink>
				</Col>
				<Col lg={12} xs={24} style={{ textAlign: !mobile && "right" }}>
					<Button type="primary" size="large" block={mobile} onClick={() => handleNext("educations")}>
						<Trans>
							Go to Educations <Icon type="right" />
						</Trans>
					</Button>
				</Col>
			</Row>
		</Col>
	)
}

export default withI18n()(Subjects)
