import React, { useState } from "react"
import { Row, Col, Form, Icon, Popconfirm, Divider, message } from "antd"
import { Heading, Button, ButtonLink, Alert, Upload, Empty } from "components"
import { Formik } from "formik"
import TextInput from "components/forms/TextInput"
import { withRouter } from "react-router"
import styled from "styled-components"
import SelectInput from "components/forms/SelectInput"
import { yearListFrom1980 } from "dummy"
import * as yup from "yup"
import { SubmitButton } from "formik-antd"
import { mutateUploadCertification, mutateDeleteCertification, mutateAddCertification, MUTATE_UPDATE_CERTIFICATE } from "queries/profile"
import { client, mobile, renderError } from "helpers"
import { useMutation } from "react-apollo"
import { Trans, t } from "@lingui/macro"
import { withI18n } from "@lingui/react"
import { country } from "App"

const FieldRow = styled.div`
	position: relative;
	&:hover {
		.delete {
			display: flex;
			justify-content: center;
			align-items: center;
			visibility: visible;
		}
	}
	.delete {
		display: none;
		cursor: pointer;
		visibility: hidden;
		position: absolute;
		right: 0;
		border-radius: 50%;
		width: 25px;
		color: #ff9d00;
		height: 25px;
		z-index: 99;
		&:hover {
			background: #eee;
		}
	}
	.ant-upload.ant-upload-select {
		display: block;
	}
`

const Drag = styled.div`
	text-align: center;
	display: block;
	padding: 2em;
	background: #f3f3f3;
	border-radius: 8px;
	margin-bottom: 2em;
	h4 {
		font-size: 1.2em;
	}
	> p {
		font-size: 4em;
	}
`

const yearList = yearListFrom1980.map((item) => ({ value: String(item), label: item }))

function Certifications({ onPrev, history, onCertifications, fetchUser, i18n }) {
	const { certifications = [], setCertifications } = onCertifications
	const [isAdding, setIsAdding] = useState(false)
	const [localFile, setLocalFile] = useState({ file: "", imagePreview: "" })

	const [uploadCertification, { loading: loadingUpload }] = useMutation(mutateUploadCertification, {
		client: client.resourceClient,
		onCompleted: (data) => {
			setLocalFile((prev) => ({ ...prev, actual: (data.uploadCertification || {}).path }))
		},
	})

	const [addCertification] = useMutation(mutateAddCertification, {
		client: client.profileClient,
		onCompleted: () => {
			fetchUser()
			message.success(i18n._(t`Your certificate has been added`))
		},
		onError: (err) => renderError(err),
	})

	const [updateCertification] = useMutation(MUTATE_UPDATE_CERTIFICATE, {
		client: client.profileClient,
		onCompleted: () => {
			fetchUser()
			message.success(i18n._(t`Your certificate has been updated`))
		},
		onError: (err) => renderError(err),
	})

	const [deleteCertification] = useMutation(mutateDeleteCertification, {
		client: client.profileClient,
		onCompleted: () => {
			fetchUser()
			message.success(i18n._(t`Your certificate has been deleted`))
		},
		onError: (err) => renderError(err),
	})

	const handleWrapup = () => {
		history.push(`/${country}/profile/info/thankyou`)
	}

	const handleDelete = (pvid) => deleteCertification({ variables: { pvid: Number(pvid) } })

	const handleCancel = () => {
		setIsAdding(false)
		setLocalFile({})
	}

	const handleUploadToLocal = (file) => {
		const reader = new FileReader()
		reader.onloadend = () => {
			setLocalFile((prev) => ({ ...prev, file, imagePreview: reader.result }))
		}
		reader.readAsDataURL(file)
		return false
	}

	const handleAddNewField = () => {
		setIsAdding(true)
	}

	const handleSave = (values, { setSubmitting }) => {
		const hasPvid = values.hasOwnProperty("pvid")
		if (!hasPvid) {
			values = {
				...values,
				relativePath: localFile.actual,
				issuedDate: values.issuedDate + "/01/01",
			}
		}
		const handler = hasPvid ? updateCertification : addCertification
		handler({ variables: values }).finally(() => {
			setIsAdding(false)
			setSubmitting(false)
			setLocalFile({})
		})
	}

	const handleUploadCert = () => {
		uploadCertification({ variables: { file: localFile.file } })
	}

	// useEffect(() => {
	//     setCertifications(certifications)
	// }, [certifications])

	return (
		<Col lg={16} xs={24} style={{ padding: "2em" }}>
			<Heading
				content={<Trans>Certifications</Trans>}
				subheader={<Trans>Please fill in your certifications</Trans>}
				level={4}
				marginBottom="2em"
			/>
			{certifications.length === 0 && !isAdding && <Empty description={<Trans>No certifications for now</Trans>} />}
			{isAdding ? (
				<Formik onSubmit={handleSave} initialValues={{ name: "", issuer: "" }} validationSchema={validationSchema}>
					{({ handleSubmit }) => (
						<Form layout="vertical" onSubmit={handleSubmit} style={{ marginBottom: "2em" }}>
							<FieldRow>
								{localFile.file ? (
									localFile.actual ? (
										<Alert
											type="success"
											showIcon
											message={
												<Trans>
													Your certificate is uploaded. Now please fill in the details of the certificate.
												</Trans>
											}
										/>
									) : (
										<Button
											block
											type="dashed"
											icon="upload"
											loading={loadingUpload}
											style={{ marginBottom: "2em" }}
											onClick={handleUploadCert}
										>
											&nbsp; <Trans>Upload this certificate now</Trans>
										</Button>
									)
								) : (
									<Upload
										beforeUpload={handleUploadToLocal}
										multiple={false}
										listType="picture"
										style={{ marginBottom: "2em", display: "block" }}
									>
										<Drag>
											<p>
												<Icon type="inbox" />
											</p>
											<Heading
												content={<Trans>Drag and drop your certification</Trans>}
												subheader={<Trans>Supported formats: .pdf, .doc, .docx, .png, .jpg, .jpeg</Trans>}
												level={4}
											/>
										</Drag>
									</Upload>
								)}
								<TextInput
									name="name"
									placeholder={i18n._(t`Name of certification`)}
									label={<Trans>Name of certification</Trans>}
									disabled={!localFile.actual}
								/>
								<Row gutter={16}>
									<Col lg={16}>
										<TextInput
											name="issuer"
											placeholder={i18n._(t`Issuer`)}
											label={<Trans>Issuer</Trans>}
											disabled={!localFile.actual}
										/>
									</Col>
									<Col lg={8}>
										<SelectInput
											name="issuedDate"
											placeholder={i18n._(t`Year issued`)}
											options={yearList}
											label={<Trans>Year issued</Trans>}
											disabled={!localFile.actual}
										/>
									</Col>
								</Row>
							</FieldRow>
							<SubmitButton type="primary" size="large">
								<Trans>Save changes</Trans>
							</SubmitButton>{" "}
							&nbsp;
							<ButtonLink size="large" onClick={handleCancel}>
								<Trans>Cancel</Trans>
							</ButtonLink>
						</Form>
					)}
				</Formik>
			) : (
				<>
					{certifications.map((item, idx) => {
						const { name = "", issuer = "", issuedDate = "", pvid } = item
						return (
							<>
								<Formik onSubmit={handleSave} initialValues={{ pvid: Number(pvid), name, issuer, issuedDate }}>
									{({ handleSubmit, dirty }) => (
										<Form layout="vertical" onSubmit={handleSubmit} style={{ marginBottom: "2em" }}>
											<FieldRow>
												<TextInput
													name="name"
													placeholder={i18n._(t`Name of certification`)}
													label={<Trans>Name of certification</Trans>}
												/>
												<Row gutter={16}>
													<Col lg={16}>
														<TextInput
															name="issuer"
															placeholder={i18n._(t`Issuer`)}
															label={<Trans>Issuer</Trans>}
														/>
													</Col>
													<Col lg={8}>
														<SelectInput
															name="issuedDate"
															placeholder={i18n._(t`Year issued`)}
															options={yearList}
															label={<Trans>Year issued</Trans>}
														/>
													</Col>
												</Row>
												<SubmitButton type="primary" disabled={!dirty}>
													<Trans>Update certificate</Trans>
												</SubmitButton>{" "}
												&nbsp;{" "}
												<Popconfirm
													title={<Trans>Delete this certificate?</Trans>}
													onConfirm={() => handleDelete(item.pvid)}
												>
													<ButtonLink icon="close">
														&nbsp;<Trans>Delete</Trans>
													</ButtonLink>
												</Popconfirm>
											</FieldRow>
										</Form>
									)}
								</Formik>
								{idx !== certifications.length - 1 && <Divider />}
							</>
						)
					})}
					<Button block type="dashed" size="medium" icon="plus" onClick={handleAddNewField} style={{ marginBottom: "2em" }}>
						&nbsp; <Trans>Add a new certificate</Trans>
					</Button>
				</>
			)}

			<Divider className="mb4em" />

			<Row type="flex" justify="space-between">
				<Col lg={12} xs={24}>
					<ButtonLink type="primary" block={mobile} size="large" onClick={() => onPrev("experiences")}>
						<Trans>
							<Icon type="left" /> Back to Experiences
						</Trans>
					</ButtonLink>
				</Col>
				<Col lg={12} xs={24} style={{ textAlign: !mobile && "right" }}>
					<Button type="primary" block={mobile} size="large" onClick={handleWrapup}>
						<Trans>
							Next <Icon type="right" />
						</Trans>
					</Button>
				</Col>
			</Row>
		</Col>
	)
}

const validationSchema = yup.object().shape({
	name: yup.string().required(`You have to tell the name of the certificate`),
	issuer: yup.string().required(`You have to tell the issuer`),
	issuedDate: yup.string().required(`You have to tell when the certificate is issued`),
})

export default withI18n()(withRouter(Certifications))
