import React, { useState, useEffect } from "react"
import { Row, Col, Form, Icon, Divider, message, Popconfirm } from "antd"
import TextInput from "components/forms/TextInput"
import { Heading, Button, ButtonLink, Empty, Loading } from "components"
import { useMutation } from "@apollo/react-hooks"
import { mutateEducations, mutateDeleteEducations, mutateUpdateEducations } from "queries/profile"
import { mobile, newClient, renderError } from "helpers"
import { Formik } from "formik"
import CheckboxInput from "components/forms/CheckboxInput"
import DateInput from "components/forms/DateInput"
import { SubmitButton } from "formik-antd"
import moment from "moment"
import { Trans, t } from "@lingui/macro"
import { withI18n } from "@lingui/react"

function Educations({ onNext, onPrev, onData, loading, fetchUser, i18n }) {
	const [userEducations, setUserEducations] = useState([])
	const [isAdding, setIsAdding] = useState(false)

	const { userData = {} } = onData
	const [addEducation] = useMutation(mutateEducations, {
		client: newClient(),
		onCompleted: () => fetchUser(),
		onError: (err) => renderError(err),
	})
	const [updateEducation] = useMutation(mutateUpdateEducations, {
		client: newClient(),
		onCompleted: () => fetchUser(),
		onError: (err) => renderError(err),
	})
	const [deleteEducation] = useMutation(mutateDeleteEducations, { client: newClient() })

	const handleDelete = (pvid) => {
		const filtered = userEducations.find((item) => item.pvid === pvid) || {}
		deleteEducation({ variables: { pvidList: [filtered.pvid] } })
			.then(() => fetchUser())
			.then(() => message.success(i18n._(t`The education item has been deleted`), 2))
	}

	const handleAdd = (values, { setSubmitting }) => {
		values = {
			...values,
			fromDate: moment(values.fromDate).format("YYYY-MM-DD"),
			toDate: values.isPresent ? moment().format("YYYY-MM-DD") : moment(values.toDate).format("YYYY-MM-DD"),
		}
		// eslint-disable-next-line no-prototype-builtins
		const hasPvid = values.hasOwnProperty("pvid")
		if (hasPvid) {
			const { source, ...rest } = values
			values = rest
		}
		const handler = hasPvid ? updateEducation : addEducation
		handler({ variables: { list: [values] } })
			.then(() => message.success(i18n._(t`The education has been updated`)))
			.then(() => {
				setSubmitting(false)
				setIsAdding(false)
			})
	}

	const handleCancel = () => {
		setIsAdding(false)
		setUserEducations(userData.educationList)
	}

	const handleAddNewField = () => setIsAdding(true)

	const renderEducations = () => {
		if ((userEducations || []).length === 0 && !isAdding) return <Empty />
		if (loading) return <Loading />
		if (isAdding) {
			return (
				<Formik onSubmit={handleAdd} initialValues={{ isPresent: false }}>
					{({ handleSubmit, values }) => (
						<Form layout="vertical" onSubmit={handleSubmit} style={{ marginBottom: "2em" }}>
							<TextInput
								name="institution"
								placeholder={i18n._(t`E.g. Nanyang Polytechnic`)}
								label={<Trans>Your school/college</Trans>}
							/>
							<Row style={{ marginBottom: "1em" }}>
								<Col lg={14}>
									<CheckboxInput name="isPresent">
										<Trans>I'm currently studying here</Trans>
									</CheckboxInput>
								</Col>
							</Row>
							<Row gutter={16}>
								<Col lg={12} xs={12}>
									<DateInput
										monthly
										name="fromDate"
										label={<Trans>From</Trans>}
										placeholder={i18n._(t`Choose the date`)}
										disabledDate={(current) => current && current > moment()}
									/>
								</Col>
								<Col lg={12} xs={12}>
									<DateInput
										monthly
										name="toDate"
										label={<Trans>To</Trans>}
										placeholder={i18n._(t`Choose the date`)}
										disabled={values.isPresent}
										disabledDate={(current) => current && current > moment()}
									/>
								</Col>
							</Row>
							<SubmitButton block>
								<Trans>Save this education</Trans>
							</SubmitButton>{" "}
							&nbsp;{" "}
							<ButtonLink block onClick={handleCancel}>
								<Trans>Cancel</Trans>
							</ButtonLink>
						</Form>
					)}
				</Formik>
			)
		}

		return (userEducations || []).map((item) => (
			<Formik
				enableReinitialize
				onSubmit={handleAdd}
				key={item.pvid}
				initialValues={{
					pvid: item.pvid,
					institution: item.institution,
					fromDate: moment(item.fromDate).format("YYYY-MM-DD"),
					toDate: moment(item.toDate).format("YYYY-MM-DD"),
					isPresent: item.isPresent,
				}}
			>
				{({ handleSubmit, values, dirty }) => (
					<Form layout="vertical" onSubmit={handleSubmit} style={{ marginBottom: "2em" }}>
						<TextInput
							name="institution"
							placeholder={i18n._(t`E.g. Nanyang Polytechnic`)}
							label={<Trans>Your school/college</Trans>}
						/>
						<Row style={{ marginBottom: "1em" }}>
							<Col lg={14}>
								<CheckboxInput name="isPresent">
									<Trans>I'm currently studying here</Trans>
								</CheckboxInput>
							</Col>
						</Row>
						<Row gutter={16}>
							<Col lg={12} xs={12}>
								<DateInput monthly name="fromDate" label={<Trans>From</Trans>} placeholder={i18n._(t`Choose the date`)} />
							</Col>
							<Col lg={12} xs={12}>
								<DateInput
									monthly
									name="toDate"
									label={<Trans>To</Trans>}
									placeholder={i18n._(t`Choose the date`)}
									disabled={values.isPresent}
								/>
							</Col>
						</Row>
						<SubmitButton disabled={!dirty}>
							<Trans>Save changes</Trans>
						</SubmitButton>{" "}
						&nbsp;{" "}
						<Popconfirm title={<Trans>Delete this education?</Trans>} onConfirm={() => handleDelete(item.pvid)}>
							<ButtonLink icon="close">
								&nbsp; <Trans>Delete</Trans>
							</ButtonLink>
						</Popconfirm>
					</Form>
				)}
			</Formik>
		))
	}

	useEffect(() => {
		setUserEducations(userData.educationList)
	}, [userData.educationList])

	return (
		<Col lg={16} xs={24} style={{ padding: "2em" }}>
			<Heading
				content={<Trans>Educational background</Trans>}
				subheader={<Trans>Please fill in your educational background</Trans>}
				level={4}
				marginBottom="2em"
			/>

			{renderEducations()}

			{!isAdding && (
				<div style={{ marginBottom: "2em" }}>
					<Button block type="dashed" size="medium" icon="plus" onClick={handleAddNewField} style={{ marginBottom: "2em" }}>
						&nbsp; <Trans>Add a new education</Trans>
					</Button>
					{/* <ButtonLink block icon="close" onClick={() => ({})}>
						&nbsp; <Trans>Cancel</Trans>
					</ButtonLink> */}
				</div>
			)}

			<Divider className="mb4em" />

			<Row type="flex" justify="space-between">
				<Col lg={12} xs={24}>
					<ButtonLink type="primary" block={mobile} size="large" onClick={() => onPrev("subjects")}>
						<Trans>
							<Icon type="left" /> Back to Subjects
						</Trans>
					</ButtonLink>
				</Col>
				<Col lg={12} xs={24} style={{ textAlign: !mobile && "right" }}>
					<Button type="primary" block={mobile} size="large" onClick={() => onNext("experiences")}>
						<Trans>
							Go to Experiences <Icon type="right" />
						</Trans>
					</Button>
				</Col>
			</Row>
		</Col>
	)
}

export default withI18n()(Educations)
