import React, { useState, useEffect } from "react"
import { Row, Col, Form, Icon } from "antd"
import { Heading, Button, ButtonLink, Loading } from "components"
import { Formik } from "formik"
import Script from "react-load-script"
import TextInput from "components/forms/TextInput"
import DateInput from "components/forms/DateInput"
import { googleApiKey } from "helpers"
import RadioInput from "components/forms/RadioInput"
import moment from "moment"
import { withI18n } from "@lingui/react"
import { Trans, t } from "@lingui/macro"
import { countryCode, countryMap } from "helpers/countryMapping"

let autocomplete = null

function AboutMe({ handlers, onData, loading, i18n }) {
	const { handleNext, handlePrev, handleUpdateProfile } = handlers
	const { userData = {} } = onData
	const [addressValue, setAddressValue] = useState("")
	const [address, setAddress] = useState({})

	const userDetails = JSON.parse(localStorage.getItem("userDetails")) || {}

	const handleLoad = () => {
		const options = {
			types: [],
			componentRestrictions: { country: userDetails.country },
		}

		/*global google*/
		autocomplete = new google.maps.places.Autocomplete(document.getElementById("location"), options)
		autocomplete.addListener("place_changed", handlePlaceSelect)
	}

	const handlePlaceSelect = () => {
		const addressObject = autocomplete.getPlace()
		const userAddress = addressObject.address_components

		if (userAddress) {
			setAddress({
				location: addressObject.formatted_address,
			})
			setAddressValue(addressObject.formatted_address)
		}
	}

	const handleSubmitBasic = (values, { setSubmitting }) => {
		const previousData = { firstName: userData.firstName, lastName: userData.lastName }
		values = {
			...values,
			...previousData,
			address: addressValue,
			phone: `${countryMap[userData.country].phoneCode}${values.phone}`,
			dob: moment(values.dob).format("YYYY-MM-DD"),
			nric: "",
		}
		handleUpdateProfile(values, "subjects")
		setSubmitting(false)
	}

	useEffect(() => {
		setAddressValue(userData.address)
	}, [userData.address])

	if (Object.keys(userData).length < 1)
		return (
			<Col lg={14} xs={24}>
				<Loading />
			</Col>
		)

	return (
		<Col lg={16} xs={24} style={{ padding: "2em 3em" }}>
			<Heading
				content={<Trans>About me</Trans>}
				subheader={
					<Trans>
						<span>Tell us some of your details</span>
					</Trans>
				}
				level={4}
				marginBottom="2em"
			/>
			<Formik
				enableReinitialize
				onSubmit={handleSubmitBasic}
				initialValues={{
					gender: (userData.gender || "").toLowerCase() || "m",
					race: userData.race || undefined,
					dob: moment(userData.dob).format("YYYY-MM-DD"),
					address: userData.address,
					description: userData.description,
					phone: (userData.phone || "").startsWith("+") ? userData.phone.slice(3) : userData.phone,
				}}
			>
				{({ handleSubmit }) => (
					<Form layout="vertical" onSubmit={handleSubmit}>
						<Row gutter={32}>
							<Col lg={12}>
								<DateInput
									name="dob"
									label={<Trans>Date of birth</Trans>}
									placeholder="Your date of birth..."
									disabledDate={(current) => current && current > moment()}
								/>
							</Col>
							<Col lg={12}>
								<TextInput
									name="phone"
									label={<Trans>Your mobile</Trans>}
									placeholder={i18n._(t`E.g. 87888788`)}
									addonBefore={countryMap[userDetails.country].phoneCode}
								/>
							</Col>
						</Row>
						<Row gutter={32}>
							{/* <Col lg={12}>
								<SelectInput
									name="race"
									label={<Trans>Race</Trans>}
									placeholder={i18n._(t`Your race...`)}
									options={race}
								/>
							</Col> */}
							<Col lg={12}>
								<Script url={googleApiKey} onLoad={handleLoad} />
								<TextInput
									special
									allowClear
									id="location"
									name="address"
									placeholder={i18n._(t`Your location...`)}
									label={<Trans>Your location</Trans>}
									value={addressValue}
									helpText={<Trans>Start typing some characters to show place options</Trans>}
									onChange={(e) => setAddressValue(e.target.value)}
								/>
							</Col>
							<Col lg={12}>
								<RadioInput
									name="gender"
									label={<Trans>Gender</Trans>}
									options={[
										{ value: "m", label: <Trans>Male</Trans> },
										{ value: "f", label: <Trans>Female</Trans> },
									]}
								/>
							</Col>
						</Row>

						<TextInput
							textarea
							rows={4}
							name="description"
							label={<Trans>Short description about you</Trans>}
							placeholder={i18n._(
								t`E.g. I am Jenny Ang, a 24-year old professional English tutor. I have 6-year experiences tutoring, both in freelance and worked under agency.`
							)}
						/>

						<br />
						<br />
						<Row type="flex" justify="space-between" align="middle">
							<Col lg={8}>
								<ButtonLink size="large" htmllType="button" icon="left" onClick={() => handlePrev("preferfield")}>
									&nbsp; <Trans>Back</Trans>
								</ButtonLink>
							</Col>
							<Col lg={8} style={{ textAlign: "right" }}>
								<Button type="primary" htmlType="submit" loading={loading}>
									<Trans>
										Next <Icon type="right" />
									</Trans>
								</Button>
							</Col>
						</Row>
					</Form>
				)}
			</Formik>
		</Col>
	)
}

export default withI18n()(AboutMe)
